<template>
    <!-- 销售应收 -->
    <div class="wrap-div" ref="wrapHeight">
        <div ref="bar_chart" class="bar-chart" :style = "{width:wrapWidth+'px',height:wrapHeight+'px'}"></div>
    </div>
</template>

<script>
    const echarts = require('echarts/lib/echarts');
    require('echarts/lib/chart/bar');
    require('echarts/lib/chart/line');
    require('echarts/lib/component/tooltip');
    require('echarts/lib/component/legend');
    export default {
        data() {
            return {
                onData:[],
                lastData:[],
                timeData:[],
                wrapHeight: 189,
                wrapWidth: 525,
            }
        },
        created(){
            this.getThisperiod();
        },
        mounted() {
            this.init();
            this.getDivHeight();
        },
        props:['weekData'],
        watch:{
            '$store.state.weekData':'getlist'
        },
        methods: {
            init() {
                setTimeout(()=>{
                    this.getlist();
                })
            },
            getDivHeight(){
                this.wrapHeight = this.$refs.wrapHeight.offsetHeight+30;
                this.wrapWidth = this.$refs.wrapHeight.offsetWidth;
            },

            //获取数据
            getlist(){
                this.getThisperiod()
                let weekDatas = [];
                this.lastData = [];
                weekDatas = this.weekData;
                if(this.$store.state.weekData.length !==0){
                    weekDatas = this.$store.state.weekData
                }
                setTimeout(()=>{
                    this.fetch('/mktmfc/forms/getreveivablesum?datetype='+this.$store.state.weekText+'&querytype=2', {})
                    .then((result)=>{
                        if(result.status === 200){
                            let arr = [];
                            arr = this.returnReconstruction(result.data.busydatelist,result.data.qtyorderlist,weekDatas);
                            this.lastData = arr[1].map(item => {
                                                return (item/10000).toFixed(2)
                                            });
                        }
                    })
                    .then(()=>{
                        this.initEchart();
                    })
                    .then(()=>{
                        this.setEchart(this.timeData,this.onData,this.lastData);
                    })
                },200)
            },

            //获取本期数据
            getThisperiod(){
                let weekDatas = [];
                this.onData = [];
                this.timeData = [];
                weekDatas = this.weekData;
                if(this.$store.state.weekData.length !==0){
                    weekDatas = this.$store.state.weekData
                }
                this.fetch('/mktmfc/forms/getreveivablesum?datetype='+this.$store.state.weekText+'&querytype=1', {})
                .then((result)=>{
                    if(result.status === 200){
                        let arr = [];
                        arr = this.returnReconstruction(result.data.busydatelist,result.data.qtyorderlist,weekDatas);
                        this.timeData = arr[0];
                        this.onData = arr[1].map(item => {
                                            return (item/10000).toFixed(2)
                                        });
                    }
                })
            },
            //echart ['12-24','12-25','12-26','12-27','12-28','12-29','12-30']
            initEchart(){
                this.bar_chart = echarts.init(this.$refs.bar_chart);
            },

            //传参
            setEchart(timeData,onData,lastData) {
                //bar
                this.bar_chart.setOption({
                    tooltip : {
                        trigger: 'axis',
                        axisPointer : {            // 坐标轴指示器，坐标轴触发有效
                            type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                        }
                    },
                    legend: {
                        data:['本期销售应收','上年同期销售应收']
                    },
                    grid: {
                        left: '13%',
                        bottom: '30%'
                    },
                    xAxis : [
                        {
                            type : 'category',
                            data: timeData ,
                            axisTick: {
                                alignWithLabel: true
                            }
                        }
                    ],
                    yAxis : [
                        {
                            type: 'value',
                            name: '万(元)',
                            boundaryGap: [0, '100%']
                        },
                    ],
                    series : [
                        {
                            name:'本期销售应收',
                            type:'bar',
                            barWidth: '30%',
                            itemStyle: {
                                normal: {
                                    color: '#008ae0',
                                    shadowColor: 'rgba(0, 0, 0, 0.5)',
                                    shadowBlur: 10
                                }
                            },
                            data:onData
                        },
                        {
                            name:'上年同期销售应收',
                            type:'line',
                            lineStyle: {
                                normal: {
                                    width: 3,
                                    shadowColor: 'rgba(0,0,0,0.4)',
                                    shadowBlur: 10,
                                    shadowOffsetY: 10
                                }
                            },
                            data:lastData
                        }
                    ]
                });
            }
        }
    }
</script>

<style scoped lang="less" type="text/less">
    .wrap-div {
        display: flex;
        height: 100%;
    }
    .bar-chart {
        /*width: 700px;*/
        /*height: 300px;*/
    }
</style>


